.Footer{
    width:100%;
    height:80px;
    background: #006064;
    overflow: hidden;
    position: absolute;
    bottom:0;
    z-index: 100;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding:10px 250px;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;

}
.Logo{
    width:60px;
    height:60px;
    overflow: hidden;
}
.logo-image{
    width:100%;
    height:100%;
}
.message{
    margin:0 20px;
    font-size:14px;
    font-weight:300;
}
.message>span{
    display: block;
    margin:5px 0;
}
.time{
    min-width:160px;
}


